import React from "react";
import { useHistory } from "react-router-dom";

const Menu = () => {
  const history = useHistory();
  const blog = () => {
    window.location.href = "https://runningboy02.tistory.com/";
  };
  const goGoogleDocs = () => {
    history.push("/report");
  };
  const ban = () => {
    history.push("/re");
  };
  return (
    <>
      <button className="center buk menubtn" onClick={blog}>
        블로그 바로가기
      </button>
      <button className="center buk menubtn" onClick={ban}>
        회원 탈퇴 및 비밀번호 변경 바로가기
      </button>
      <button className="center buk menubtn" onClick={goGoogleDocs}>
        건의사항 및 신고
      </button>
    </>
  );
};
export default Menu;
