import { authService } from "fbase";
import React from "react";
import { useHistory } from "react-router-dom";

// 공지사항에 모바일에서만 버전 가져와 보여주기
const Re = ({ userObj }) => {
  const history = useHistory();
  const repassword = () => {
    authService.sendPasswordResetEmail(userObj.email).then(() => {
      alert(userObj.email + "로 비밀번호 변경 메일을 발송 했습니다.");
    });
  };
  const delacc = () => {
    const user = authService.currentUser;
    var name = prompt(
      "계정을 삭제하려면 " + userObj.displayName + "를 입력하세요."
    );
    if (name === userObj.displayName) {
      user.delete().then(() => {
        alert("계정이 영구적으로 삭제되었습니다.");
        history.push("/");
      });
    } else {
      alert("일치하지 않습니다. 계정이 그대로 유지됩니다");
    }
  };
  return (
    <>
      <button onClick={repassword}>비밀번호 재설정</button>
      <button onClick={delacc} className="delacc">
        계정 영구 삭제
      </button>
    </>
  );
};
export default Re;
