import React from "react";

// 공지사항에 모바일에서만 버전 가져와 보여주기
const Notice = ({ userObj }) => {
  const ver = () => {
    window.location.href = "https://runningboy02.tistory.com/5";
  };
  return (
    <>
      <span className="center buk" Style="font-size: 30px">
        v 1.3.3부터 패치 보고서가 완전히 블로그로 이동하고 공지사항이 블로그로
        이동했습니다.
        <br />
        <br />
        지금 버전 : {userObj.v}
        <br />
        <button className="ver buk" onClick={ver}>
          지금 버전 최신 확인
        </button>
      </span>
    </>
  );
};
export default Notice;
