import { dbService, storageService } from "fbase";
import React, { useState } from "react";
import { v4 as uuidv4 } from "uuid";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faTimes } from "@fortawesome/free-solid-svg-icons";

const NweetFactory = ({ userObj }) => {
  const [nweet, setNweet] = useState("");
  const [attachment, setAttachment] = useState("");
  const [color, setColor] = useState("#FFFFFF");

  const onSubmit = async (event) => {
    let isMobile = false;
    var UserAgent = navigator.userAgent;
    if (
      UserAgent.match(
        /iPhone|iPod|Android|Windows CE|BlackBerry|Symbian|Windows Phone|webOS|Opera Mini|Opera Mobi|POLARIS|IEMobile|lgtelecom|nokia|SonyEricsson/i
      ) != null ||
      UserAgent.match(/LG|SAMSUNG|Samsung/) != null
    ) {
      isMobile = true;
    }
    event.preventDefault();
    let attachmentUrl = "";
    if (nweet !== "") {
      if (attachment !== "") {
        const attachmentRef = storageService
          .ref()
          .child(`${userObj.uid}/${uuidv4()}`);
        const response = await attachmentRef.putString(attachment, "data_url");
        attachmentUrl = await response.ref.getDownloadURL();
      }
      var creatorEmail;
      if (userObj.email !== null) {
        var a = userObj.email.split("@")[0];
        var b = "*".repeat(userObj.email.split("@")[1].split(".")[0].length);
        var c = userObj.email.split("@")[1].split(".")[1];
        creatorEmail = a + "@" + b + "." + c;
      }
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth();
      var clockDate = date.getDate();
      var hours = date.getHours();
      var minutes = date.getMinutes();
      var seconds = date.getSeconds();
      var Tim =
        `${year}년 ${month + 1}월 ${clockDate}일 ` +
        `${hours < 10 ? `0${hours}` : hours}:${
          minutes < 10 ? `0${minutes}` : minutes
        }:${seconds < 10 ? `0${seconds}` : seconds}`;
      const nweetObj = {
        text: nweet,
        createdAt: Date.now(),
        creatorId: userObj.uid,
        attachmentUrl,
        creatorEmail: creatorEmail,
        creatorName: userObj.displayName,
        createdTime: Tim,
        isMobile: isMobile,
        color: color,
        adminemail: userObj.email,
      };
      await dbService.collection("nweets").add(nweetObj);
      setNweet("");
      setAttachment("");
    } else {
      alert("빈 문장은 입력이 불가능합니다");
    }
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNweet(value);
  };
  const onColor = (event) => {
    const {
      target: { value },
    } = event;
    setColor(value);
  };
  const onFileChange = (event) => {
    const {
      target: { files },
    } = event;
    const theFile = files[0];
    const reader = new FileReader();
    reader.onloadend = (finishedEvent) => {
      const {
        currentTarget: { result },
      } = finishedEvent;
      setAttachment(result);
    };
    reader.readAsDataURL(theFile);
  };
  const onClearAttachment = () => setAttachment("");
  return (
    <form onSubmit={onSubmit} className="factoryForm">
      <div className="factoryInput__container">
        <input
          className="factoryInput__input"
          value={nweet}
          onChange={onChange}
          type="text"
          placeholder="어떤생각을 하고있나요?"
          maxLength={120}
        />
        <input type="submit" value="&rarr;" className="factoryInput__arrow" />
      </div>
      <label for="attach-file" className="factoryInput__label">
        <span>사진 추가</span>
        <FontAwesomeIcon icon={faPlus} />
      </label>
      <input
        id="attach-file"
        type="file"
        accept="image/*"
        onChange={onFileChange}
        style={{
          opacity: 0,
        }}
      />
      {attachment && (
        <div className="factoryForm__attachment">
          <img
            src={attachment}
            style={{
              backgroundImage: attachment,
            }}
          />
          <div className="factoryForm__clear" onClick={onClearAttachment}>
            <span>Remove</span>
            <FontAwesomeIcon icon={faTimes} />
          </div>
        </div>
      )}
      {userObj.email && (
        <>
          <label for="nweedColor" className="colorrr buk">
            로그인을 하여 게시물 박스에 색을 설정 할 수 있음
          </label>
          <input
            type="color"
            value="#ffffff"
            className="colorr"
            id="nweetColor"
            onChange={onColor}
          />
        </>
      )}
    </form>
  );
};
export default NweetFactory;
